'use client';
import { useEffect } from 'react';

export default function CartScript() {
  useEffect(() => {
    // Create and append script to body
    const script = document.createElement('script');
    script.src = '/js/simple-test.js';
    script.async = true;
    document.body.appendChild(script);
    
    console.log("Script tag added to page");
    
    // Testing localStorage directly in React component
    try {
      localStorage.setItem('reactTest', 'React can use localStorage');
      const testValue = localStorage.getItem('reactTest');
      console.log("React localStorage test:", testValue);
    } catch (error) {
      console.error("React localStorage error:", error);
    }
    
    // Clean up on unmount
    return () => {
      if (script.parentNode) {
        document.body.removeChild(script);
      }
    };
  }, []);

  return null;
}